import { Injectable } from '@angular/core';
import { JSONBuilder } from './JSONBuilder.service';
declare var jQuery: any;
@Injectable()
export class ShareOutcomeService {
    shareLinks: any = {};
    private selectedFormula: any;
    constructor(private _jsonBuilderHelper: JSONBuilder) {
        // if (!this.selectedFormula)
        //     this.setSelectedFormula(0);
    }

    setSelectedFormula(formula: any) {
        if (!formula)
            formula = this._jsonBuilderHelper.getJSONBuilt().formula[0];

        this.setResultButtonCTA(formula);
        this.selectedFormula = formula;
        /** sharelink object init */
        this.setSharelinks();
    }

    setResultButtonCTA(formula: any) {
        if (this._jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation' && formula) {
            this._jsonBuilderHelper.getJSONBuilt().navigate_Url = formula.units.postValue;
            /** set text */
            for (var page of this._jsonBuilderHelper.getJSONBuilt().pages) {
                if (page.type === 'Result') {
                    for (var section of page.sections) {
                        for (var item of section.items) {
                            if (item.type == 'leadform' || item.type == 'click_button')
                                item.props.title = formula.units.preValue;
                        }
                    }
                }
            }
        }
    }

    getSelectedFormula(): any {
        this.textareaSize(); /*DESIGNER FXN */
        return this.selectedFormula;
    }

    textareaSize() {
        /*DESIGNER CHANGES */
        let self = this;
        var leftHeight = jQuery('.recom-section .left-sec').height();
        var rightHeight = jQuery('.recom-section .outer-main').height();
        if (leftHeight > rightHeight)
            jQuery('.outer-main').css('height', leftHeight);
        else
            jQuery('.left-outer').css('height', rightHeight);
        if (jQuery('.big-text').prop('scrollHeight') > 50)
            jQuery('.big-text').css('height', jQuery('.big-text').prop('scrollHeight'));
        else
            jQuery('.big-text').css('height', 50);
    }

    setSharelinks() {
        let self = this;
        if (self.selectedFormula && self.selectedFormula.links && self.selectedFormula.links.length) {
            self.selectedFormula.links.map((link: any) => {
                if (self.shareLinks[link.type]) {
                    self.shareLinks[link.type][link.socialType] = link;
                } else {
                    self.shareLinks[link.type] = {};
                    self.shareLinks[link.type][link.socialType] = link;
                }
            });
        }
    }

    getSharelinks(): any {
        return this.shareLinks;
    }
}


